<!-- Reservation.vue -->
<template>
  <div class="property-park">
<!--    <van-nav-bar left-arrow @click-left="goBack" title="访客预约"></van-nav-bar>-->
    <navigation-bar :pageName="pageName" @onLeftClick="goBack">
    </navigation-bar>
    <div class="search-box">
      <van-search v-model="carNum" placeholder="请输入车牌号码查询" @search="onSearch" />
      <span class="search-btn" @click="onSearch(carNum)">搜索</span>
    </div>
    <div class="address">{{address}}</div>
    <div style="overflow: scroll; height: 80%">
      <van-pull-refresh v-model="isPulling" @refresh="onRefresh">
        <div class="park-list" v-show="parkList.length>0">
          <van-list
                  v-model="loading"
                  :finished="finished"
                  @load="onLoad"
                  finished-text="—— 我是有底线的 ——"
                  :offset="10"
          >
            <div class="item" v-for="(item,index) in parkList" :key="index">
              <div>
                <div>{{item.plateNum}}</div>
                <div class="time" :class="(item.status.indexOf('停留')!=-1)?'stay':''">{{item.status}}</div>
                <!-- <div class="time" v-if="item.overTime">已超过{{item.overTime}}分钟</div> -->
                <!-- <div class="time stay" v-if="item.stayTime">已停留{{item.stayTime}}分钟</div> -->
              </div>
              <div class="right">
                <button class="btn" @click="goDetail(item)">查看详情</button>
              </div>
            </div>
          </van-list>
        </div>
        <div class="no-list" v-show="parkList.length==0">—— 暂无数据 ——</div>
      </van-pull-refresh>
    </div>

  </div>
</template>

<script>
import { Search, PullRefresh } from "vant";
import { store } from "@/utils/store";
import { selectRegion } from "xt/services/recognition.js";
import { getParkingList, getRefreshTime } from "xt/services/register";
import NavigationBar from "../../../title/NavigationBar";
export default {
  name: "PropertyPark",
  components: {
    NavigationBar,
    [Search.name]: Search,
    [PullRefresh.name]: PullRefresh
  },
  data() {
    return {
      pageName:"访客预约",
      carNum: "",
      regionId: "20180820013",
      address: "未知",

      loading: false,
      isPulling: false, // 下拉刷新
      finished: false, // 列表数据是否全部加载完
      pageNum: 1,
      total: 0,
      parkList: [],
      refreshTime: 0,
      timer: null
    };
  },
  mounted() {
    this.address = store.get("custNews").AREA_NAME || "未知";
    this.getParkingList();
    this.getParkTime();
  },
  destroyed() {
    clearInterval(this.timer);
  },
  computed: {
    getTimeStr(time) {
      let day = parseInt(time / day); // 天
    }
  },
  methods: {
    goBack() {
      this.$router.push({
        path: "/"
      });
    },
    // 获取小区名字
    getCommunityName() {
      let data = {
        regionId: this.regionId
      };
      this.loading = true;
      let that = this;
      selectRegion(data)
        .then(res => {
          that.loading = false;
          that.address = res.data || "未知";
        })
        .catch(err => {
          that.loading = false;
          that.$toast(err);
        });
    },
    // 获取列表刷新时间
    getParkTime() {
      this.loading = true;
      let that = this;
      getRefreshTime()
        .then(res => {
          that.loading = false;
          if (res.code == 200) {
            that.refreshTime = parseInt(res.data[0].extend) * 60000;
            if (that.timer) {
              clearInterval(that.timer);
            } else {
              if (that.refreshTime == 0) {
                console.log("我不刷新");
                return;
              }
              that.timer = setInterval(() => {
                this.pageNum = 1;
                this.parkList = [];
                that.getParkingList();
                console.log("列表刷新啦~");
              }, that.refreshTime);
            }
          } else {
            that.$toast(res.message);
          }
        })
        .catch(err => {
          that.loading = false;
          that.$toast(err);
        });
    },
    getParkingList() {
      this.loading = true;
      let that = this;
      let data = {
        isHistory: 2,
        stationId: 1,
        page: this.pageNum,
        pageSize: 10,
        nameLike: this.carNum
      };
      getParkingList(data)
        .then(res => {
          that.loading = false;
          if (res.code == 200) {
            // let list = that.parkList.concat(res.data.list || []);
            // list.forEach(item => {
            //   if (item.status.indexOf("停留") != -1) {
            //     let time = parseInt(item.status.split("停留")[1]);
            //     let hour = parseInt(time / 60);
            //     let min = parseInt(time % 60);
            //     item.status =
            //       "已停留" + hour == 0
            //         ? `${min}分钟`
            //         : min == 0
            //         ? `${hour}小时`
            //         : `${hour}小时${min}分钟`;
            //   } else {
            //     let time = parseInt(item.status.split("超时")[1]);
            //     let hour = parseInt(time / 60);
            //     let min = parseInt(time % 60);
            //     item.status =
            //       "已停留" + hour == 0
            //         ? `${min}分钟`
            //         : min == 0
            //         ? `${hour}小时`
            //         : `${hour}小时${min}分钟`;
            //   }
            // });
            that.parkList = that.parkList.concat(res.data.list || []);
            that.total = res.data.total;
          } else {
            that.$toast(res.message);
          }
          that.finished = false;
          if (that.parkList.length >= that.total) {
            that.finished = true; //此时不再执行onLoad事件
          }
        })
        .catch(err => {
          that.loading = false;
          that.$toast(err);
        });
    },
    onSearch(val) {
      this.carNum = val;
      this.pageNum = 1;
      this.parkList = [];
      this.getParkingList();
    },
    //下拉刷新
    onRefresh() {
      // if (this.loading) {
      //   return;
      // }
      setTimeout(() => {
        this.isPulling = false;
        this.finished = false;
        this.pageNum = 1;
        this.parkList = [];
        this.getParkingList();
      }, 1000);
    },
    //上拉加载
    onLoad() {
      // if (this.loading) {
      //   return;
      // }
      this.finished = false;
      if (this.parkList.length >= this.total) {
        this.finished = true; //此时不再执行onLoad事件
        return;
      }
      setTimeout(() => {
        this.isPulling = false;
        this.finished = false;
        this.pageNum++;
        console.log("上拉加载");
        this.getParkingList();
        // 如果当前展示的列表长度 == total
      }, 500);
    },
    goDetail(obj) {
      this.$router.push({
        path: "/parkDetail",
        query: obj
      });
    }
  }
};
</script>
